import React from 'react';
import './FoodMenu.css'

const FoodMenu=(props)=>{
    var j=0;
    const data=[]
    for(j=0;j<4;j++){
        data.push(props.data[j])
    }
    return(
    <div className="Container">
        <div className="topContainer">
            <img className="IMG" src="food.webp" alt="imagehere" width="90" height="90"/> 
            <button onClick={()=>{props.handleTransition("FoodItemDisplayState",props.CategoryName)}} className="select">{props.data[0].main}</button>
        </div>
        <div className="topContainer">
        {
            data.map((i)=>{
                  return (
                      <div className="summary">
                           <div className="nameContainer">
                                <p className="highlight1">{i.name}</p>
                                <p className="price">price <strong>{i.price}$</strong></p>
                            </div>        
                            <p>{i.about}</p>

                      </div>
                      )
            })
        }
        </div>
        
    </div>)
}
export default FoodMenu